import React from 'react';

import Step from './Instruction/Step/Step';
import Instruction from './Instruction/Instruction';

import classes from './Route.module.css';

const Route = (props) => {  
 
    if(props.selected != null && props.selected !== props.index)
        return null;
    
    let steps = [];
    let instructions;

    const modifySteps = () => {
        const MAX_ALLOWED = 4;
        const legs = props.journey.legs;
               
        steps = legs.map((type, i) => { 
            if((type.mode.id !== 'walking') || (type.mode.id === 'walking' && legs.length === 1) || (type.mode.id === 'walking' && type.duration >= 12)) {
                return <Step type = { type.mode.id } duration = { type.duration } route = { type.instruction.summary.split(' ')[0] } key = { i } /> 
            } 
            return null;
        });

        steps = steps.filter(el => el != null);
        
        if (steps.length > MAX_ALLOWED) {  
            let length = steps.length;
            steps.length = MAX_ALLOWED;
            steps.push(<Step key = { Math.random() } type = 'more' amount = { length - MAX_ALLOWED } />);
        } 

        let i = 1;
        while (i < steps.length) {
            steps.splice(i, 0, <div className = { classes.Dot } key = { Math.random() } />);
            i += 2;
        }
    }

    const modifyInstructions = () => {
        if (props.selected != null && props.selected === props.index) { 
            instructions = props.journey.legs.map((instruction, i) => {
                let result;
                let type = instruction.mode.id;

                switch (type) {
                    case 'walking':
                        result = 
                            <Instruction 
                                type = { 'walk' } 
                                command = { instruction }
                                key = { i } />
                        break;
                        
                    case 'cycle':
                        result = 
                            <Instruction 
                                type = { 'wait' } 
                                command = { instruction }
                                key = { i } />
                        break;

                    default:
                        result = 
                            <React.Fragment key = { i }>
                                <Instruction 
                                    type = { 'wait' } 
                                    command = { instruction } />
                                <Instruction
                                    type = { 'ride' }
                                    command = { instruction } />
                            </React.Fragment>
                        break;
                }

                return result;
            });
        }
    }

    const expandRoute = () => {
        props.click(props.index);
    }

    modifySteps();
    modifyInstructions();

    return (
        <div className = { [classes.Main, props.selected === props.index && classes.Expanded].join(' ') }>
            <section className = { classes.Route } onClick = { () => expandRoute() }>
                <div className = { classes.Steps }>
                    { steps }
                </div>
                <div className = { classes.Information }>
                    { props.journey.price && <div className = { classes.Price }>{ props.journey.price }</div> }
                    <div className = { classes.Duration }>
                        <p className = { classes.Number }>{ props.journey.duration }</p>
                        <p className = { classes.Min }>MIN</p>
                    </div>
                </div>
            </section>

            <section className = { classes.Selection }>
                { instructions }
            </section>
        </div>
    );
}

export default Route;
