import React from 'react';

import classes from './Step.module.css';

const step = ({ type, route, amount }) => {
    let icon;

    switch (type) {
        case 'walking':
            icon = <div className = { classes.Walking } />;
            break;
        case 'cycle':
            icon = <div className = { classes.Cycle } />;
            break;
        case 'bus':
            icon = <div className = { classes.Bus } />;
            break;
        case 'tube':
            icon = <div className = { classes.Tube } />;
            break;
        case 'overground':
            icon = <div className = { classes.Tube } />;
            break;
        case 'national-rail':
            icon = <div className = { classes.NationalRailway } />;
            break;
        case 'tflrail':
            icon = <div className = { classes.Rail } />
            break;
        default:
            icon = <div className = { classes.Rail } />
            break;
    }

    let info;

    switch (type) {
        case 'bus':
            info = route;
            break;
        case 'more':
            info = '+' + amount;
            break;
        default:
            break;
    }
    
    return (
        <div className = { classes.Step }>
            { icon }
            <p className = { classes.Info }>{ info }</p>
        </div>
    )
}

export default step;
