import React, { Component } from 'react';

import classes from './Time.module.css';

class Time extends Component {
    state = {
        day: 'Today',
        date: '',
        time: '',
        daysOpen: false
    }

    componentDidMount () {        
        let now = new Date();

        let date = now.getFullYear() + 
            ((now.getMonth() + 1).toString().length !== 2 ? '0' : '') + (now.getMonth() + 1) +
            (now.getDate().toString().length !== 2 ? '0' : '') + now.getDate();

        let time = (now.getHours().toString().length < 2 ? '0' : '') + now.getHours() + ':' +
            (now.getMinutes().toString().length < 2 ? '0' : '') + now.getMinutes();

        this.props.onChange(date, time.replace(':', ''));
        this.setState({ date: date, time: time })
    }

    openDaysHandler = () => {
        this.setState(prevState => { return { daysOpen: !prevState.daysOpen } });
    }

    dateChangeHandler = (theDay, days, event) => {
        event.preventDefault();
             
        let result = new Date();
        result.setDate(result.getDate() + days);
        
        let date = 
            result.getFullYear() + 
            ((result.getMonth() + 1).toString().length !== 2 ? '0' : '') + (result.getMonth() + 1) +
            (result.getDate().toString().length !== 2 ? '0' : '') + result.getDate();
        
        this.props.onChange(date, this.state.time.replace(':', ''));        
        this.setState({ day: theDay });
    }

    timeChangeHandler = (time) => {
        this.props.onChange(this.state.date, time.target.value.replace(':', ''));        
        this.setState({ time: time.target.value });        
    }

    render () {
        let dateRows = ['today', 'tomorrow'];
        
        let today = new Date();
        let daysOfWeek = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
        
        for (let i = 2; i < 7; i++) {
            let day = daysOfWeek[(today.getDay() + i) % 7];
            dateRows.push(day);
        }  

        let days = <div className = { classes.Days }>
            { dateRows.map((d, i) => {
                return <button 
                        className = { classes.Day } 
                        onClick = { (event) => this.dateChangeHandler(d, i, event) }
                        key = { d }>{ d }</button>;
            }) }
        </div>;

        return (
            <div className = { classes.Category }>
                <label className = { classes.Label }>{ this.props.label }</label>
                <div className = { classes.Types }>
                    <div className = { [classes.Option, this.state.daysOpen && classes.DropdownOpen].join(' ') } 
                         onClick = { this.openDaysHandler }>
                        { this.state.day }
                        { this.state.daysOpen && days }
                        <div className = { [classes.DropdownArrow, this.state.daysOpen && classes.DropdownArrowOpen].join(' ') } />
                    </div>
                    <input 
                        type = 'time' 
                        className = { classes.Option } 
                        value = { this.state.time } 
                        onChange = { this.timeChangeHandler } />
                </div>
            </div>
        );
    }
}

export default Time;
