import React, { Component } from 'react';

import Layout from './hoc/Layout/Layout';
import Brand from './components/Brand/Brand';

import Menu from './containers/Menu/Menu';
import Header from './components/Header/Header';
import Main from './components/Main/Main';
import Map from './components/Map/Map';

class App extends Component {
  state = {
    trip: null,
    location: null,
    plan: 'planning'
  }

  journeyPlannedHandler = (data, plan, location) => {
    this.setState({ trip: data, plan: plan, location: location });
  }

  render () {
    return (
      <Layout>
        <Menu>
          <Header trip = { this.state.trip } replan = { this.journeyPlannedHandler } />
          <Main plan = { this.state.plan } replan = { this.journeyPlannedHandler } />
        </Menu>
        { this.state.plan === 'planned' ? <Map location = { this.state.location } /> : <Brand /> }
      </Layout>
    );
  }    
}

export default App;
