import React, { useState, useEffect } from 'react';
import axios from 'axios';

import Form from '../../containers/Form/Form';
import Spinner from '../UI/Spinner/Spinner';
import Routes from '../../containers/Routes/Routes';
import Error from '../UI/Error/Error';

import { useStore } from '../../hooks-store/store';

import classes from './Main.module.css';

const Main = (props) => {	
	const [journey, setJourney] = useState();
	const [loaded, setLoaded] = useState(false);
	const [error, setError] = useState();

	const [transit, setTransit] = useState();
	const [cycle, setCycle] = useState();

	const dispatch = useStore()[1];
	
	useEffect(() => {
		checkIfLoaded();
	});
		
	const checkIfLoaded = () => {
		if(transit != null && cycle != null && journey != null && !loaded) {
			dispatch('ADD_ROUTE', { route: null });
			dispatch('DEFINE_LOCATION', { location: journey[1] });
			
			setLoaded(true);

			props.replan(journey[0], 'planned', journey[1]);
		}
	}

	const submitFormHandler = (trip) => {
		setTransit(null);
		setCycle(null);
		setError(null);
		setJourney(null);
		setLoaded(false);

		props.replan(null, 'loading', null);

		fetchTransitHandler(trip);
		fetchCyclingHandler(trip);
	}

	const fetchTransitHandler = (trip) => {
		const app_id = 'f1cc3b6c';
        const app_key = 'ba60f0558e0e09bd4d9fa4feb9d9bf81';
		const url = `https://api.tfl.gov.uk/Journey/JourneyResults/${ trip.location }/to/${ trip.destination }?date=${ trip.date }&time=${ trip.time }&timeIs=${ trip.timeIs }&app_id=${ app_id }&app_key=${ app_key }`;

        axios.get(url)
            .then(response => { 
				let jn = { 
					location: response.data.journeyVector.from, 
					destination: response.data.journeyVector.to, 
					timeIs: trip.timeIs,
					from: trip.from,
					to: trip.to,
					time: response.data.searchCriteria.dateTime
				};

				const firstJourney = response.data.journeys[0];
				const start = { lat: firstJourney.legs[0].departurePoint.lat, lng: firstJourney.legs[0].departurePoint.lon };
				const end = { lat: firstJourney.legs[firstJourney.legs.length - 1].arrivalPoint.lat, lng: firstJourney.legs[firstJourney.legs.length - 1].arrivalPoint.lon };
				
				setJourney([jn, [start, end]]);
				setTransit(response.data);  
			})
            .catch(err => {
				setError(err);
				if(error != null)
					props.replan(null, 'error', null);
			});
	}

	const fetchCyclingHandler = (trip) => {
		const app_id = 'f1cc3b6c';
        const app_key = 'ba60f0558e0e09bd4d9fa4feb9d9bf81';
		const url = `https://api.tfl.gov.uk/Journey/JourneyResults/${ trip.location }/to/${ trip.destination }?mode=${ 'cycle' }&app_id=${ app_id }&app_key=${ app_key }`;

        axios.get(url)
            .then(response => { 
				setError(null);
				setCycle(response.data);  
			})
            .catch(error => {
				setError(error);
				if(error != null)
					props.replan(null, 'error', null);
			});
	}

	const replanJourneyHandler = () => {
		setTransit(null);
		setCycle(null);
		setError(null);
		setJourney(null);
		setLoaded(false);
		
		props.replan(null, 'planning', null);
	}

	return (
		<section className = { classes.Main }>
			{ props.plan === 'planning' ? 
				<Form submit = { submitFormHandler } /> 
				: props.plan === 'loading' ?
					<Spinner />
					: props.plan === 'planned' ? 
						<Routes transit = { transit } cycle = { cycle } />
						: <Error error = { error } replan = { replanJourneyHandler } /> }
		</section>
	);
}

export default Main;
