import React, { useState } from 'react';
import { useStore } from '../../hooks-store/store';

import Route from '../../components/Route/Route';

import classes from './Routes.module.css';

const Routes = (props) => {
    const [currentTransport, setCurrentTransport] = useState('transit');
    const [selected, setSelected] = useState();
    const dispatch = useStore()[1];

    let routes;
    
    const routeSelectedHandler = (index, route) => {
        dispatch('ADD_ROUTE', selected !== index ? { route: route } : { route: null });
        setSelected(selected != null ? null : index)
    }
    
    const removeRepetativeRoutes = () => {
        let uniqueness = props.transit.journeys.map(route =>
            route.legs.map(type => type.instruction.summary.split(' ')[0]).join(''));

        for (let i = 0; i < uniqueness.length; i++)
            for (let y = 0; y < i; y++)
                if (uniqueness[i] === uniqueness[y])
                    routes.splice(y, 1);
    }

    const loadRoutesForTransport = (transport) => {
        if (transport === 'transit') {
            routes = props.transit.journeys.map((route, i) => 
                <Route 
                    journey = { route }  
                    selected = { selected }
                    click = { (index) => routeSelectedHandler(index, route) }
                    index = { i } 
                    key = { i } />);

            removeRepetativeRoutes();
        } else if (currentTransport === 'cycle') {
            routes = 
                <Route 
                    journey = { props.cycle.journeys[0] } 
                    selected = { selected } 
                    index = { 0 } 
                    click = { (index) => routeSelectedHandler(index, props.cycle.journeys[0]) } 
                    index = { 0 } />;
        }
    }

    const switchTransportHandler = (event, transport) => {
        if(event) event.preventDefault();

        if(transport !== currentTransport) {
            dispatch('ADD_ROUTE', { route: null });
            loadRoutesForTransport(transport);
            setCurrentTransport(transport);  
            setSelected(null);          
        }
    }

    loadRoutesForTransport(currentTransport);
        
    return (
        <section className = { classes.Journeys }>
            <nav className = { classes.Transports }>
                <button className = { classes.Transport } onClick = { (event) => switchTransportHandler(event, 'transit') }><div className = { [classes.Public, currentTransport === 'transit' ? classes.Selected : ''].join(' ') }/></button>
                <div className = { classes.Border } />
                <button className = { classes.Transport } onClick = { (event) => switchTransportHandler(event, 'cycle') }><div className = { [classes.Cycle, currentTransport === 'cycle' ? classes.Selected : ''].join(' ') }/></button>
             </nav>

            <nav className = { classes.Routes }>
                { routes }
            </nav>
        </section>
    );
}

export default React.memo(Routes);
