import React, { Component } from 'react';

import Input from '../../components/UI/Input/Input';
import Select from '../../components/UI/Select/Select';
import Time from '../../components/UI/Time/Time';

import classes from './Form.module.css';

class Form extends Component {
    state = {
		trip: {
			location: '',
            destination: '',
            from: '',
            to: '',
			timeIs: 'departing',
			date: '',
			time: ''
		},
		dayOptionsOpen: true
    }

    locationInputHandler = (name, value) => {
		this.setState({...this.state, trip: { ...this.state.trip, location: value, from: name } });
	}

	destinationInputHandler = (name, value) => {
		this.setState({...this.state, trip: { ...this.state.trip, destination: value, to: name } });
	}

	timeIsInputHandler = (type) => {
		if(type !== this.state.trip.timeIs) 
			this.setState({...this.state, trip: { ...this.state.trip, timeIs: type } });
	}

	dateInputHandler = (date, time) => {
		if(this.state.date === undefined && this.state.time === undefined)
			this.setState({...this.state, trip: { ...this.state.trip, date: date, time: time } });
		else if (date !== this.state.trip.date)
			this.setState({...this.state, trip: { ...this.state.trip, date: date } });
		else if (time !== this.state.trip.time)
			this.setState({...this.state, trip: { ...this.state.trip, time: time } });
	}

	sumbitGoHandler = (event) => {
		event.preventDefault();
		this.props.submit(this.state.trip);
	}
    
    render () {
        const inputValuesMissing = () => {
			if(this.state.trip.location === '' || this.state.trip.destination === '' ||
				this.state.trip.date === '' || this.state.trip.time === '')
				return true;

			return false;
        }
        
        return (
            <form className = { classes.Inputs }>
                <Input 
                    label = 'Where are you?' 
                    placeholder = 'Enter your location'
                    onChange = { this.locationInputHandler }
                    currentLocation>                        
                </Input>

                <Input
                    label = 'Where do you want to go?'
                    placeholder = 'Enter your destination'
                    onChange = { this.destinationInputHandler } />

                <Select 
                    label = 'I am..'
                    selected = { this.state.trip.timeIs }
                    options = { ['departing', 'arriving'] }
                    onChange = { this.timeIsInputHandler } />

                <Time
                    label = 'When?'
                    date = { this.state.trip.date }
                    onChange = { this.dateInputHandler } />

                <button 
                    className = { classes.Go } 
                    onClick = { this.sumbitGoHandler }
                    disabled = { inputValuesMissing() }>Go</button>
            </form>
        );
    }
}

export default Form;
