import { initStore } from './store';

const configureStore = () => {
    const actions = {
        ADD_ROUTE: (curState, payload) => {
            return { ...curState, ...payload };
        },
        DEFINE_LOCATION: (curState, payload) => {
            return { ...curState, ...payload }
        }
    }

    initStore(actions, { 
        route: null,
        location: null
    });
};

export default configureStore;
