import React, { useState } from 'react';

import PlacesAutocomplete, { geocodeByAddress } from 'react-places-autocomplete';

import classes from './Input.module.css';

const Input = (props) => {
    const [address, setAddress] = useState('');

    const handleSelect = async (value) => {
        const results = await geocodeByAddress(value);
        const latLng = results[0].geometry.location.lat().toString() + ',' + results[0].geometry.location.lng().toString();
        
        props.onChange(value, latLng);

        setAddress(value);
    }

    const getCurrentLocationHandler = (event) => {
        event.preventDefault();

        navigator.geolocation.getCurrentPosition(
            position => {   
                const latLng = position.coords.latitude.toString() + ',' + position.coords.longitude.toString();
        
                props.onChange('Current location', latLng);

                setAddress('Current location');
            }
        )
    }

    return (
    	<div className = { classes.Category }>
            <label className = { classes.Label }>{ props.label }</label>
            <PlacesAutocomplete value = { address } onChange = { (value) => { setAddress(value); props.onChange(value, value) } } onSelect = { handleSelect }>
                {
                    ({ getInputProps, suggestions, getSuggestionItemProps, loading }) => (
                        <React.Fragment>
                            <input className = { classes.Input } { ...getInputProps({ placeholder: props.placeholder }) } />

                            <div className = { classes.SuggestionBox }>
                                { loading ? <div className = { classes.LoadingBox }>Loading..</div> 

                                 : suggestions.map((suggestion) => {
                                    const style = {
                                        backgroundColor: suggestion.active ? '#41b6e6' : '#fff',
                                        cursor: 'pointer'
                                    }
                                    return <div { ...getSuggestionItemProps(suggestion, { className: [classes.Suggestion, suggestion.active ? classes.SuggestionActive : ''].join(' ') }) }>{ suggestion.description }</div>
                                }) }
                            </div>
                        </React.Fragment>
                    )
                }
            </PlacesAutocomplete>

            { props.currentLocation && 
                <button onClick = { getCurrentLocationHandler } className = { classes.Additional } tabIndex = '-1'>
                    <div className = { [classes.LocationIcon, address === 'Current location' ? classes.CurrentLocationEnabled : ''].join(' ') } />
                </button> 
            }
        </div>
    );
}

export default Input;
