import React from 'react';

import { Marker } from 'react-google-maps';
import BUS_ICON from '../../../../assets/images/Map/bus-stop-icon.png';
import TUBE_ICON from '../../../../assets/images/Map/underground-icon.png';
import OVERGROUND_ICON from '../../../../assets/images/Map/overground-icon.png';
import NATIONALRAIL_ICON from '../../../../assets/images/Map/national-rail-icon.png';
import TFLRAIL_ICON from '../../../../assets/images/Map/tflrail-icon.png';
import START_ICON from '../../../../assets/images/Map/start-icon.png';
import END_ICON from '../../../../assets/images/Map/end-icon.png';

const Points = ({ type, route, location }) => {
    let markers = [];
    let icon = null;

    switch (type) {
        case 'bus':
           icon = BUS_ICON;
           break;
        case 'tube':
            icon = TUBE_ICON;
            break;            
        case 'overground':
            icon = OVERGROUND_ICON;
            break;                
        case 'national-rail':
            icon = NATIONALRAIL_ICON;
            break;
        case 'tflrail':
            icon = TFLRAIL_ICON;
            break; 
        case 'start':
            icon = START_ICON;
            break;
        case 'end':
            icon = END_ICON;
            break;
        default:
            icon = '';
            break;
    }

    const size = type === 'start' || type === 'end' ? 45 : 35;

    const displayIcon = {
        url: icon,
        scaledSize: new window.google.maps.Size(size, size)
    };

    if (location != null) {
        markers.push(<Marker icon = { displayIcon } key = { Math.random() } position = {{ lat: location.lat, lng: location.lng }} />);
    }

    if (route != null) {
        markers.push(
            <Marker 
                icon = { displayIcon }
                position = {{ lat: route.departurePoint.lat, lng: route.departurePoint.lon }} 
                key = { Math.random() } />);
        
        markers.push(
            <Marker 
                icon = { displayIcon }
                position = {{ lat: route.arrivalPoint.lat, lng: route.arrivalPoint.lon }} 
                key = { Math.random() } />);
    }

    return markers;
}

export default Points;
