import React from 'react';

import Platform from '../Platform/Platform';

import classes from './Wait.module.css';

const walk = ({ vehicle, name }) => {
    let icon;

    switch (vehicle) {
        case 'bus':
            icon = <div className = { classes.Bus } />;
            break;

        case 'tube':
            icon = <div className = { classes.Tube } />;
            break;

        case 'overground':
            icon = <div className = { classes.Tube } />;
            break;

        case 'national-rail':
            icon = <div className = { classes.NationalRailway } />;
            break;

        case 'tflrail':
            icon = <div className = { classes.Rail } />
            break;

        default:
            break;
    }

    return (
        <section className = { classes.Instructions }>
            <div className = { classes.Instruction }>
                <div className = { classes.Dot } />
                <div className = { classes.Summary }>
                    { vehicle === 'bus' && 'Wait for' } { icon } { vehicle === 'bus' ? <Platform name = { name.split(' ')[0] } /> : name }
                </div>
            </div>
            
            <div className = { classes.Information }>

            </div>
        </section>
    );
}

export default walk;
