import React from 'react';

import { Polyline } from 'react-google-maps';

const Path = ({ type, coordinates, location }) => {
    let route;

    let dot = {
        path: 'M 0,0 0,0',
        strokeOpacity: 0.95,
        strokeWeight: 6,
        scale: 4
    };

    let line = {
        path: 'M 0,0 1,0',
        strokeOpacity: 0.95,
        strokeWeight: 6,
        scale: 4
    };

    switch (type) {
        case 'walking':
            route = <Polyline path = { coordinates } defaultOptions = {{ 
                strokeOpacity: 0, 
                strokeColor: '#000A66',
                icons: [{
                    icon: dot,
                    offset: '50%',
                    repeat: '14px'
                }] }} />;
            break;        
        case 'cycle':
            route = <Polyline path = { coordinates } defaultOptions = {{ strokeColor: '#444444', strokeWeight: '3', strokeOpacity: '0.7' }} />;
            break;   
        case 'bus':
            route = <Polyline path = { coordinates } defaultOptions = {{ strokeColor: '#FF3C3C', strokeWeight: '4', strokeOpacity: '0.7' }} />;
            break;   
        case 'tube':
            route = <Polyline path = { coordinates } defaultOptions = {{ strokeColor: '#3C6AFF', strokeWeight: '4', strokeOpacity: '0.7' }} />;
            break; 
        case 'overground':
            route = <Polyline path = { coordinates } defaultOptions = {{ strokeColor: '#EE7D11', strokeWeight: '4', strokeOpacity: '0.7' }} />;
            break; 
        case 'national-rail':
            route = <Polyline path = { coordinates } defaultOptions = {{ strokeColor: '#9700B2', strokeWeight: '4', strokeOpacity: '0.7' }} />;
            break;  
        case 'tflrail':
            route = <Polyline path = { coordinates } defaultOptions = {{ strokeColor: '#8E8E8E', strokeWeight: '4', strokeOpacity: '0.7' }} />;
            break;  
        case 'none':
            route = <Polyline path = { location } defaultOptions = {{ strokeColor: '#444444', strokeWeight: '4', strokeOpacity: '0.7' }} />;
            break;              
        default:
            route = <Polyline path = { coordinates } defaultOptions = {{ strokeColor: '#444444', strokeWeight: '4' }} />;;
            break;
    }

    return route;
}

export default Path;
