import React from 'react';

import Walk from './Walk/Walk';
import Wait from './Wait/Wait';
import Ride from './Ride/Ride';

import classes from './Instruction.module.css';

const instruction = ({ type, command }) => {
    let result;

    switch (type) {
        case 'walk': 
            result = 
                <Walk 
                    name = { command.arrivalPoint.commonName } 
                    letter = { command.arrivalPoint.stopLetter }
                    duration = { command.duration } />; 
            break;

        case 'wait': 
            result = 
                <Wait
                    vehicle = { command.mode.id }
                    name = { command.instruction.summary } />; 
            break;

        case 'ride': 
            result = 
                <Ride
                    departure = { command.departurePoint }
                    arrival = { command.arrivalPoint }
                    stops = { command.path.stopPoints }
                    duration = { command.duration } />; 
            break;

        default: 
            break;
    }
   
    return (
    	<section className = { classes.Main }>
    		{ result }
    	</section>
    );
}

export default instruction;
