import React from 'react';

import classes from './Error.module.css';

const error = (props) => {
    return (
    	<div className = { classes.Error }>
            <p className = { classes.Oops }>Oops!</p>
    		<h1 className = { classes.Status }>{ props.error.response.status }</h1>
            <p className = { classes.Info }>Seems like<br/>something is wrong!</p>
            <p className = { classes.Message }>{ props.error.response.message }</p>
            <button className = { classes.Replan } onClick = { props.replan }>Replan</button>
    	</div>
    );
}

export default error;
