import React from 'react';

import classes from './Header.module.css';

const header = (props) => {
	let date = props.trip ? new Date(props.trip.time).toString().split(' ') : [];
	date.length = 5;

	let content = props.trip ?
		<div className = { classes.Trip }>
			<div className = { classes.Info }>
				<p>From: <span>{ props.trip.from }</span></p>
				<p>To: <span>{ props.trip.to }</span></p>
				<p>{ props.trip.timeIs }: <span>{ date.join(' ') }</span></p>
			</div>
			<button className = { classes.Replan } onClick = { () => props.replan(null, 'planning') }>Replan</button>
		</div> 
		: <h1 className = { classes.Plan }>Plan your journey</h1>;
		
    return (
    	<div className = { classes.Header }>
    		{ content }
    	</div>
    );
}

export default header;
