import React from 'react';

import classes from './Brand.module.css';

const brand = () => {
    return (
        <div className = { classes.Brand }>
            <div className = { classes.Journey }>
                <h1>Journey Planner</h1>
            </div>
            
            <div className = { classes.London }>
                <h1>For London</h1>
            </div>
        </div>
    );
}

export default brand;
