import React from 'react';

import Path from './Path/Path';
import Points from './Points/Points';

import { useStore } from '../../../hooks-store/store';

const Directions = () => {    
    let paths = useStore()[0].route;
    let location = useStore()[0].location;

    if(paths) {
        paths = paths.legs.map((type, i) => {
            const line = JSON.parse(type.path.lineString).map(c => { return { lat: c[0], lng: c[1] } });

            return (<React.Fragment key = { i }>
                        <Points type = { type.mode.id } route = { type } />
                        <Path type = { type.mode.id } coordinates = { line } location = { location } />;
                    </React.Fragment> );
        });
    }



    return (
        <React.Fragment>
            <Points type = 'start' location = { location[0] } />
            <Points type = 'end' location = { location[1] } />
            { paths ? paths.map(d => d) : <Path type = 'none' location = { location } /> }
        </React.Fragment>
    );
}

export default Directions;
