import React, { useState } from 'react';

import Duration from '../Duration/Duration';
import Platform from '../Platform/Platform';

import classes from './Ride.module.css';

const Ride = ({ departure, arrival, stops, duration }) => {
    const [expanded, setExpanded] = useState(false);

    let busStops = expanded && <ul className = { classes.Names }> { stops.map((stop, i) => i !== stops.length - 1 && 
        <li className = { classes.Name } key = { i }>
            <div className = { classes.Dot } />
            { stop.name }
        </li>
    )}</ul>;

    return (
        <React.Fragment>
            <section className = { classes.Instructions }>
                <div className = { classes.Instruction }>
                    <div className = { classes.Dot } />

                    <div className = { classes.Summary }>
                        Ride { stops.length } { stops.length > 1 ? 'stops' : 'stop' }
                    </div>
                </div>
                
                <div className = { classes.Information }>
                    <Duration duration = { duration } />  
                </div>
            </section>

            <section className = { classes.Stops }>
                <div className = { classes.Location }>
                    <Platform name = { departure.stopLetter ? departure.stopLetter : 'M' } />
                    <p className = { classes.Point }>{ departure.commonName }</p>
                </div>
                <div className = { classes.Expand }>
                    <div className = { classes.Collapse } onClick = { () => setExpanded(!expanded) }>
                        { stops.length > 1 && <div className = { [classes.Arrow, expanded ? classes.Expanded : ''].join(' ') } /> }
                        <p className = { classes.Show }>{ stops.length } { stops.length > 1 ? 'stops' : 'stop' }</p>
                    </div>
                    { busStops }
                </div>
                <div className = { classes.Location }>
                    <Platform name = { arrival.stopLetter ? arrival.stopLetter : 'M' } />
                    <p className = { classes.Point }>{ arrival.commonName }</p>
                </div>
            </section>
        </React.Fragment>
    );
}

export default Ride;
